.no-margin {
    margin: 0;
    padding: 0;
  }
  
  .pageBody {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin: 0 auto;
  }
  
  .page {
    color: black;
    width: 1000px;
    height: 100%;
    border-right: 1px solid rgba(0, 0, 0, 0.25);
    border-left: 1px solid rgba(0, 0, 0, 0.25);
    margin: 30px 0px 40px 0px;
    min-height: 600px;
  }
  
  .pageSideMenu {
    width: 220px;
    height: 300px;
    height: 100%;
    text-align: center;
    margin-top: 30px;
  }
  
  .pageTitle {
    position:relative;
    font-size: 30px;
    font-family: Poppins, sans-serif;
    width: 80%;
    margin-left: 80px;
  }
  
  .pageContent {
    position:relative;
    font-size: '14px';
    font-family: Poppins, sans-serif;
    width: 90%;
    margin: 0 auto;
  }
  
  .section {
    margin-top: 30px;
    width: 100%;
  }

  .text-field {
    font-family: 'Poppins', sans-serif;
    color: black;
    font-size: 16px;
    margin-bottom: 30px;
  }

  .break {
    margin-bottom: 30px;
  }